import axios from 'axios';

const API_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5002';

const apiClient = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json',
  },
  timeout: 60000, // 60Áßí„ÅÆ„Çø„Ç§„É†„Ç¢„Ç¶„Éà
});

// „ÉÅ„É£„ÉÉ„ÉàAPI
export interface ChatMessage {
  message: string;
  session_id?: string;
}

export interface ChatResponse {
  answer?: string;
  response?: string;
  type?: string;
  error?: string;
}

export const chatApi = {
  /**
   * „ÉÅ„É£„ÉÉ„Éà„É°„ÉÉ„Çª„Éº„Ç∏„ÇíÈÄÅ‰ø°
   */
  sendMessage: async (message: string, sessionId?: string): Promise<ChatResponse> => {
    const response = await apiClient.post<ChatResponse>('/api/unified/chat', {
      message,
      session_id: sessionId,
    });
    return response.data;
  },

  /**
   * ‰ºöË©±„ÇíÈñãÂßã
   */
  startConversation: async (sessionId?: string): Promise<ChatResponse> => {
    const response = await apiClient.post<ChatResponse>('/start_conversation', {
      session_id: sessionId,
    });
    return response.data;
  },
};

// Â∑•Â†¥„ÉÄ„ÉÉ„Ç∑„É•„Éú„Éº„ÉâAPI
export interface FactoryCase {
  id: string;
  title: string;
  status: string;
  created_at: string;
  [key: string]: any;
}

export const factoryApi = {
  /**
   * Ê°à‰ª∂‰∏ÄË¶ß„ÇíÂèñÂæó
   */
  getCases: async (status?: string): Promise<FactoryCase[]> => {
    try {
      const params = status ? { status } : {};
      const response = await apiClient.get<{ success: boolean; cases: FactoryCase[] }>('/admin/api/cases', { params });
      
      if (response.data.success && response.data.cases) {
        return response.data.cases;
      }
      
      // ‰∫íÊèõÊÄß„ÅÆ„Åü„ÇÅ„ÄÅcases„ÅåÁõ¥Êé•ÈÖçÂàó„ÅÆÂ†¥Âêà„ÇÇÂØæÂøú
      if (Array.isArray(response.data)) {
        return response.data;
      }
      
      return [];
    } catch (error: any) {
      console.error('Ê°à‰ª∂ÂèñÂæóAPI„Ç®„É©„Éº:', error);
      if (error.response) {
        console.error('„É¨„Çπ„Éù„É≥„Çπ:', error.response.data);
        console.error('„Çπ„ÉÜ„Éº„Çø„Çπ:', error.response.status);
      }
      throw error;
    }
  },

  /**
   * Ê°à‰ª∂„ÅÆ„Çπ„ÉÜ„Éº„Çø„Çπ„ÇíÊõ¥Êñ∞
   */
  updateCaseStatus: async (pageId: string, status: string): Promise<void> => {
    await apiClient.post('/admin/api/update-status', {
      page_id: pageId,
      status,
    });
  },

  /**
   * „Ç≥„É°„É≥„Éà„ÇíËøΩÂä†
   */
  addComment: async (pageId: string, comment: string): Promise<void> => {
    await apiClient.post('/admin/api/add-comment', {
      page_id: pageId,
      comment,
    });
  },
};

// Â∑•Â†¥„Éû„ÉÉ„ÉÅ„É≥„Ç∞APIÔºà„Éï„Çß„Éº„Ç∫4-1Ôºâ
export interface CaseInfo {
  category?: string;
  user_message?: string;
  customer_location?: string;
  prefecture?: string;
}

export interface MatchedFactory {
  factory_id: string;
  name: string;
  prefecture: string;
  specialties: string[];
  matching_score: number;
  score_details: {
    location_score: number;
    specialty_score: number;
    workload_score: number;
    rating_score: number;
    total_score: number;
  };
  [key: string]: any;
}

export const factoryMatchingApi = {
  /**
   * Ê°à‰ª∂„Å´ÊúÄÈÅ©„Å™Â∑•Â†¥„Çí„Éû„ÉÉ„ÉÅ„É≥„Ç∞
   */
  matchFactories: async (
    caseInfo: CaseInfo,
    maxResults: number = 5
  ): Promise<MatchedFactory[]> => {
    try {
      const response = await apiClient.post<{
        success: boolean;
        matched_factories: MatchedFactory[];
        count: number;
      }>('/api/v1/factories/match', {
        case: caseInfo,
        max_results: maxResults,
      });

      if (response.data.success && response.data.matched_factories) {
        return response.data.matched_factories;
      }

      return [];
    } catch (error: any) {
      console.error('Â∑•Â†¥„Éû„ÉÉ„ÉÅ„É≥„Ç∞API„Ç®„É©„Éº:', error);
      throw error;
    }
  },

  /**
   * Ê°à‰ª∂„ÇíËá™ÂãïÁöÑ„Å´ÊúÄÈÅ©„Å™Â∑•Â†¥„Å´Ââ≤„ÇäÂΩì„Å¶
   */
  autoAssignCase: async (
    caseId: string,
    caseInfo: CaseInfo
  ): Promise<MatchedFactory | null> => {
    try {
      const response = await apiClient.post<{
        success: boolean;
        assigned_factory?: MatchedFactory;
        error?: string;
      }>(`/api/v1/cases/${caseId}/auto-assign`, {
        case: caseInfo,
      });

      if (response.data.success && response.data.assigned_factory) {
        return response.data.assigned_factory;
      }

      return null;
    } catch (error: any) {
      console.error('Ê°à‰ª∂Ëá™ÂãïÂâ≤„ÇäÂΩì„Å¶API„Ç®„É©„Éº:', error);
      throw error;
    }
  },
};

// „Éë„Éº„Éà„Éä„Éº‰øÆÁêÜÂ∫óAPIÔºà„Éï„Çß„Éº„Ç∫4-2Ôºâ
